.container {
  padding: 10px;
  width: 300px;
  border: 1px solid;
  border-radius: 4px;
}

.title {
  padding: 0;
  margin: 0;
  margin-top: 10px;
}

.input {
  margin-bottom: 20px;
  border: 1px solid;
  border-radius: 4px;
}

.btnSubmit {
  display: block;
  border: 1px solid;
  color: rgb(63, 94, 251);
  border-radius: 4px;
  font-weight: 500;
  background: radial-gradient(
    circle,
    rgba(238, 174, 202, 1) 0%,
    rgba(148, 187, 233, 1) 100%
  );
  cursor: pointer;
  transition: 0.4s;
  color: #f0eaea;
}

.btnSubmit:hover {
  background: radial-gradient(
    circle,
    rgba(63, 94, 251, 1) 0%,
    rgba(252, 70, 107, 1) 100%
  );
}
